* {
    font-family: Arial, Helvetica, sans-serif;
    box-sizing: border-box;
  }
  
  body {
    /*
    background: #8d1717;
    color: hsl(200, 100%, 90%);
    max-width: 400px;
    padding: 1rem;
    margin: 0 auto;
    */

    height: 100vh;
  background-color: rgb(25, 155, 241);
  background-image: url('https://images.unsplash.com/photo-1484480974693-6ca0a78fb36b?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D&auto=format&fit=crop&w=1472&q=80');
  background-size: cover;
  background-position: center;
  }

  .container{
    color: white;
    background-color: rgba(255, 255, 255, 0.8);
    border-radius: 7px;
    box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.5);
    padding: 25px;
    height:100px;
    width: 60%;
    justify-content: center;
    display: flex;
    
}
  
  .new-item-form {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    margin: 1rem 4rem;
    
    padding:1rem;
    background: rgba(217, 225, 230, 0.7);
  backdrop-filter: blur(15px);
  border-radius: 7px;
  z-index: 1;
    
    

  }
  
  .form-row {
    
    display: flex;
    flex-direction: column;
    gap: 0.1rem;
    
  }

  .form-row#item{

    padding:0.5rem;
  }

  .form-row label{

    font-weight: 600;
  }

  .items{
    display: flex;
    width: 100%;
    gap:2rem;
    justify-content: space-between;
    padding-bottom: 0.3rem;
    padding:0.4rem;
    vertical-align: middle;
    

  }

  .items .item {
    
    font-weight: 600;
    vertical-align: middle;
   


  }
  .items .del-btn {
    justify-content: end;


  }

  

  
  
  .btn {
    background: hsl(200, 100%, 50%, 0.7);
    border: 1px solid hsl(200, 100%, 50%);
    color: hsl(200, 95%, 24%);
    padding: 0.25em 0.5em;
    padding:0.5rem;
    border-radius: 7px;
    cursor: pointer;
    outline: none;
    font-size: 1rem;
    font-weight: 600;
    color: white;
    
  }

  .btn .add-button {
    padding: 5rem 5rem;
    padding-top: 2rem;

  }
  
  .btn:hover,
  .btn:focus-visible {
    background: hsl(200, 100%, 50%, 0.8);
  }
  
  .btn.btn-delete {
    background: hsl(0, 100%, 40%, 0.1);
    border: 1px solid hsl(0, 100%, 40%);
    color: hsl(0, 100%, 40%);
    display: flex;
    justify-content: end;
  }
  
  .btn.btn-delete:hover,
  .btn.btn-delete:focus-visible {
    background: hsl(0, 100%, 40%, 0.2);
  }
  
  .new-item-form input {
    outline: none;
    border: 1px solid hsl(200, 100%, 40%);
    background: hsl(200, 19%, 91%);
    border-radius: 7px;
    padding: 0.25em 0.5em;
    padding:0.4rem;
    color: hsl(200, 94%, 14%);
    font-size: 1.2rem;
  }
  
  .new-item-form input:focus {
    border: 1px solid hsl(200, 100%, 70%);
  }
  
  .header {
    font-size: 1.5rem;
    margin-top: 0rem;
    margin-bottom: 0.5rem;
    margin:0rem 4rem;
    
    padding:0.3rem;
    padding-left: 1.3rem;
    background: rgba(217, 225, 230, 0.7);
  backdrop-filter: blur(15px);
  border-radius: 7px;
  }
  
  .list {
    margin: 0;
    
    margin-left: 1rem;
    list-style: none;
    margin: 1rem 4rem;
    padding:0.3rem;
    padding-left: 1.6rem;
    padding-right: 0.9rem;
    background: rgba(217, 225, 230, 0.7);
  backdrop-filter: blur(15px);
  border-radius: 7px;
  
  }
  
  .list li:has(input:checked) label {
    color: hsl(200, 20%, 40%);
  }
  
  .list {
    display: flex;
    flex-direction: column;
    gap: 0.3rem;
  }
  
  .list li {
    display: flex;
    gap: 0.5rem;
    align-items: center;
  }
  
  .list li label {
    display: flex;
    gap: 0.25rem;
    cursor: pointer;
    align-items: center;
  }
  
  .list li:has(input:focus-visible) label {
    outline: 1px solid hsl(200, 100%, 50%);
  }
  
  .list li input {
    outline: none;
    width: 0;
    height: 0;
    appearance: none;
    pointer-events: none;
    position: absolute;
  }
  
  .list li label::before {
    content: "";
    display: block;
    width: 0.9rem;
    height: 0.9rem;
    background: hsl(200, 100%, 90%);
    border-radius: 0.25em;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .list li label:hover::before {
    background: hsl(200, 100%, 80%);
  }
  
  .list li:has(input:checked) label::before {
    content: "✔";
    background: hsl(200, 100%, 40%);
    color: hsl(200, 100%, 90%);
    font-size: 0.75rem;
    font-weight: bold;
  }
  
  .list li:has(input:checked) label:hover::before {
    background: hsl(200, 100%, 30%);
  }