*{
  margin: 0;
  padding: 0;
  box-sizing: border-box ;
  font-family: 'Open Sans', sans-serif;
}

html {scroll-behavior: smooth;}
body{
  height: 100vh;
  background-color: rgb(182, 27, 27);
  background-image: url('https://images.unsplash.com/photo-1551309292-e185c0b6e22a?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D&auto=format&fit=crop&w=1169&q=80');
  background-size: cover;
  background-position: center;
}

li{
  list-style: none;
  text-shadow: 0px 2px 10px rgba(250, 245, 245, 0.3);
}

a{
  text-decoration: none;
  color: white;
  font-size: 1rem;
}
a:hover {
  color: orange;
  

}

/* Sticky */

.sticky {
  position: fixed;
  top: 0;
  width: 100%;
}

.sticky + .contentX {
  padding-top: 60px;
}

/* Header */

header {
  position: relative;
  padding: 0 2rem;
  background: rgb(255, 255, 255, .1);
  backdrop-filter: blur(15px);
  z-index: 9999;;
}

.navbar {
  width: 100%;
  height: 60px;
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  

}

.navbar .logo a {

  font-size: 1.5rem;
  font-weight: bold;
  text-transform:capitalize;
  letter-spacing: 1px;
  
}

.navbar .links {
  display: flex;
  gap: 2rem;
}

.navbar .toggle_btn{

  color: white;
  font-size: 1.5rem;
  cursor: pointer;
  display: none;
}

.action_btn{
  background-color: orange;
  color: white;
  padding: 0.5rem 1rem;
  border: none;
  outline: none; 
  border-radius: 7px;
  font-size: 0.8rem;
  font-weight: bold;
  cursor: pointer;
  transition: scale 0.2 ease;
}

.action_btn:hover{
  scale: 1.05;
  color: white;
}

.action_btn:active{
  scale: 0.95;
}


/* Dropdown menu */

.dropdown_menu {
  display: none;
  position: absolute;
  right: 2rem;
  top: 80px;
  height: 0px;
  width: 300px;
  background: rgba(22,28,31, 0.7);
  backdrop-filter: blur(15px);
  border-radius: 7px;
  overflow: hidden;
  transition: height 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.dropdown_menu.open{
  height: 240px;

}

.dropdown_menu li{
  padding: 0.7rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.dropdown_menu .action_btn{
  width: 90%;
  display: flex;
  justify-content: center;
}


/* Responsive part */

@media(max-width: 992px){
  .navbar .links,
  .navbar .action_btn {
      display: none;
  }
  .navbar .toggle_btn{
      display: block;
  }
  
  .dropdown_menu{
      display: block;
  }
  
  }
  
  @media(max-width: 576px){
  
      .dropdown_menu{
          left: 2rem;
          width: unset;
      }
  }
  
  
  
  
